function renderMediaUploader( args ) {
    'use strict';
 
    var file_frame, image_data;
 
    /**
     * If an instance of file_frame already exists, then we can open it
     * rather than creating a new instance.
     */
    if ( undefined !== file_frame ) {
 
        file_frame.open();
        return;
    }

    // Create the file_frame
    file_frame = wp.media.frames.file_frame = wp.media({

        title: args.title,
        library: { type: 'image' },
        button: { text: args.button },
        multiple: false
    });
 
    // Fire the select event
    file_frame.on( 'select', function() {
 
		var selection = file_frame.state().get('selection').first().toJSON();

		//console.log(args.inputField);

		var inputField = jQuery(args.inputField);
		var imgTag = jQuery(args.imgTag);
		inputField.val(selection.url);
		imgTag.attr('src', selection.url);
	 		
		//console.log( selection);
    });

    // Now display the actual file_frame
    file_frame.open();
 
}
 
(function( $ ) {
    'use strict';
 
    $(function() {

    	// Template Manager Star Rating Image
        $( '#rwp-rating-image-btn' ).on( 'click', function( e ) {
            e.preventDefault();
            var args = {
            	'title': 		$(this).data('tb-title'),
            	'button': 		$(this).data('tb-button'),
            	'inputField': 	'#rwp-rating-image-url',
            	'imgTag': 		'#rwp-rating-image'
            }

            // Display the media uploader
            renderMediaUploader(args);
 
        });

        // Review Image
        $('#rwp-reviews-meta-box').on('click','.rwp-review-add-image-btn', function( e ) {
            e.preventDefault();
            var reviewId = $(this).data('review-id');
            var args = {
            	'title': 		$(this).data('tb-title'),
            	'button': 		$(this).data('tb-button'),
            	'inputField': 	'#rwp-review-add-image-input-'+reviewId,
            	'imgTag': 		'#rwp-review-image-img-'+reviewId
            }

            // Display the media uploader
            renderMediaUploader(args);
 
        });
 
    });
 
})( jQuery );